import { makeStyles } from "@material-ui/core";
import { useEffect, useState } from "react";
import Hero from "../models/Hero";
import MockPartyService from "../services/MockPartyService";

const useStyles = makeStyles({
    appHeader: {
        backgroundColor: "#282c34",
        minHeight: "100px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        fontSize: "calc(10px + 2vmin)",
        color: "white",
    },
    headerSpan: {
        width: "100%",
        display: "flex",
    },
    headerParty: {
        position: "absolute",
        zIndex: 1000,
        right: "10px",
        top: "30px",
    },
    headerImg: {
        width: "90px",
        height: "90px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "#282c34",
        margin: "4px",
    },
    appLogo: {
        height: "70px",
        pointerEvents: "none",
        padding: "20px",
    },
});

type HeaderProps = {};

function Header(props: HeaderProps) {
    const classes = useStyles();

    let [party, setParty] = useState(null as Hero[] | null); // The map graph data

    useEffect(() => {
        // Initial load of party
        const loadParty = () => {
            const heroService = new MockPartyService();
            const newParty = heroService.getParty();
            if (!newParty) {
                console.error(`No party data`);
                return;
            }

            setParty(newParty);
        };

        if (party == null) {
            loadParty();
        }
    });

    const renderParty = () => {
        if (!party) return null;

        return party.map((hero) => {
            return (
                <a key={hero.id} href={`/#/profile/${hero.id}`}>
                    <img src={hero.imageUrl} className={classes.headerImg} alt="profile" />
                </a>
            );
        });
    };

    return (
        <header className={classes.appHeader}>
            <span className={classes.headerSpan}>
                <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                <img className="title shadow" alt="Tales from the Pit" src="title.svg" />
                <span className={classes.headerParty}>{renderParty()}</span>
            </span>
        </header>
    );
}

export default Header;
