import { Grid, makeStyles, Typography } from "@material-ui/core";
import { Tooltip } from "@material-ui/core";
import React from "react";
import { FunctionComponent, useState } from "react";
import Item from "../models/Item";

const useStyles = makeStyles({
    main: {
        textAlign: "center",
        width: "100%",
    },
    container: {
        position: "relative",
        height: "180px",
        width: "400px",
        margin: "auto",
    },
    gridItem: {
        height: "85px",
    },
    inventory: {
        padding: "10px 30px",
        position: "absolute",
    },
    item: {
        width: "100%",
        border: "solid",
        borderRadius: "15%",
        borderWidth: "3px",
        borderColor: "lightgrey",
        cursor: "pointer",
        backgroundColor: "whitesmoke",
    },
    itemSelected: {
        width: "100%",
        border: "solid",
        borderRadius: "15%",
        borderWidth: "3px",
        borderColor: "cadetblue",
        cursor: "pointer",
        backgroundColor: "powderblue",
    },
});

type InventorySelectorProps = {
    onSelectionChanged?: (ids: string[]) => boolean;
    multiSelect?: boolean;
    items: Item[];
};

const InventorySelector: FunctionComponent<any> = (props: InventorySelectorProps) => {
    const classes = useStyles();
    const { onSelectionChanged, items, multiSelect } = props;

    let [selected, setSelected] = useState([] as boolean[]);

    const onSelect = (ev: React.MouseEvent<HTMLImageElement, MouseEvent>) => {
        const id = Number.parseInt(ev.currentTarget.id);

        const newSelected = multiSelect ? selected.map((t) => t) : [];

        newSelected[id] = !newSelected[id];
        setSelected(newSelected);

        const selectedIds: string[] = [];
        for (let i = 0; i < 8; i++) {
            if (newSelected[i]) selectedIds.push(items[i].id);
        }

        if (onSelectionChanged) {
            const result = onSelectionChanged(selectedIds);
        }
    };

    const renderInventory = () => {

        let index = 0;
        const maxItems = 8;

        const itemGrids = items.map((item) => {
            const imgClass = selected[index] ? `${classes.itemSelected}` : `${classes.item}`;
            const output = (
                <Grid item xs={3} className={classes.gridItem}>
                    <Tooltip
                        title={
                            <React.Fragment>
                                <Typography color="inherit">{item.name}</Typography>
                                {item.description}
                            </React.Fragment>
                        }
                        key={index}
                        arrow
                    >
                        <img src={item.imageUrl} id={index.toString()} onClick={onSelect} className={imgClass} alt={item.name} />
                    </Tooltip>
                </Grid>
            );
            index++;
            return output;
        });

        const emptyItems = [];
        for (let i = items.length; i < maxItems; i++) {
            emptyItems.push("");
        }

        const emptyGrids = emptyItems.map(i => {
            const imgClass = selected[index] ? `${classes.itemSelected}` : `${classes.item}`;
            const output = (
                <Grid item xs={3} className={classes.gridItem}>
                    <Tooltip title={"Nothing"} key={index}>
                        <img src={"/images/items/empty.png"} id={index.toString()} className={imgClass} alt="nothing" />
                    </Tooltip>
                </Grid>
            );
            index++;
            return output;
        });

        return itemGrids.concat(emptyGrids);
    };

    return (
        <div className={classes.main}>
            <div className={classes.container}>
                <div className={classes.inventory}>
                    <Grid container spacing={3} alignItems="center" justify="space-evenly">
                        {renderInventory()}
                    </Grid>
                </div>
            </div>
        </div>
    );
}

export default InventorySelector;
